@import url('https://fonts.googleapis.com/css2?family=Comfortaa:wght@300&display=swap');

*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Comfortaa', cursive;
  color: #61DAFB;
  user-select:none;
}
body{
  background-color: #282c34;

}
.App {
  text-align: center;
}
.timerNumbers{
  width: fit-content;
  font-size: 6vw;
  margin: 50px auto;
  display: grid;
  /* grid-template-columns: 40px 24px 40px 24px 40px 24px 40px; */
  grid-template-columns: 30px 18px 30px 18px 30px 18px 30px;
}
@media(min-width:400px){
  .timerNumbers{
    grid-template-columns: 50px 30px 50px 30px 50px 30px 50px;
    font-size: 30px;
    /* grid-template-columns: repeat(5,1fr); */
  }
}
.timerButtons{
  max-width: 400px;
  margin: 0 auto;
  display: grid;
  grid-template-columns: repeat(3,1fr);
  margin-bottom: 50px;
}
@media(min-width:400px){
  .timerButtons{
    grid-template-columns: repeat(5,1fr);
  }
}
button{
  cursor: pointer;
  color: black;
  padding: 15px;
}
.loops{
  max-height: 600px;
  overflow-y: scroll;
  scrollbar-width: none;
}
.loops::-webkit-scrollbar {
  display: none;
}
.loop{
  max-width: 190px;
  position: relative;
  display: grid;
  margin: 0px auto;
  padding: 10px 0;
  grid-template-columns: 40px 1fr;
  
  border-bottom: 1px solid #61DAFB;
  /* margin: 5px; */
}
.loop:last-child{
  border-bottom: none;

}
.loopId{
  font-weight: bold;
}
.afterBeforeNumber{
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: -2vw;
}
@media(min-width:400px){
  .afterBeforeNumber{
    margin-top: -7px;
  }
}